import React, { Component } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { firestoreConnect } from 'react-redux-firebase';
import { Rnd } from 'react-rnd';
import ReactDOM from "react-dom";

class LabelBox extends Component {
    constructor(props){
        super(props);
    }

    state = {
        x: this.props.box.x_position,
        y: this.props.box.y_position,
        width: this.props.box.width,
        height: this.props.box.height,
    }

    showBorders = () =>{
        const node = ReactDOM.findDOMNode(this);

        if (node instanceof HTMLElement) {
            const child = node.querySelectorAll('.resize-handle');
            child.forEach(corner => {
                corner.style.display = "block";
            })
        }
        document.querySelector('.main-properties').style.display = "none";
        document.querySelector('.properties').style.display = "inline-block";
        document.getElementById('property-name').value = this.props.box.name;
        document.getElementById('property-font').value = this.props.box.fontSize/this.props.zoomLevel;
        document.getElementById('property-bg').value = '#'+this.props.box.background;
        document.getElementById('property-bg-color').value = '#'+this.props.box.borderColor;
        document.getElementById('property-bg-thick').value = this.props.box.borderThickness/this.props.zoomLevel;
        document.getElementById('property-bg-radius').value = this.props.box.borderRadius/this.props.zoomLevel;

        this.props.updateList(null, this.props.index);
    }

    handleClick = (e) => {
        const node = ReactDOM.findDOMNode(this);
        if (node.contains(e.target)) {
            return;
        }
        else if(this.hasParent(e.target,'stay')){
            return;
        }
        else if(this.hasParent(e.target,'resize-handle')){
            return;
        }
        else{
            this.props.noCurrentItem();
            this.hideBorders();
        }
    }

    hasParent(element, classname) {
        if (element.className === undefined) return false;
        if (element.className.split(' ').indexOf(classname) >= 0) return true;
        return element.parentNode && this.hasParent(element.parentNode, classname);
    }
    componentDidMount(){
        document.addEventListener('mousedown',this.handleClick);
    }
    componentWillUnmount() {
        document.removeEventListener('mousedown',this.handleClick);
    }

    hideBorders(){
        const node = ReactDOM.findDOMNode(this);

        if (node instanceof HTMLElement) {
            const child = node.querySelectorAll('.resize-handle');
            child.forEach(corner => {
                corner.style.display = "none";
            })
        }
        document.querySelector('.properties').style.display = "none";
        document.querySelector('.main-properties').style.display = "inline-block";
    }

    updateBoxList(){
        console.log("Hi");
        this.props.updateList(this.props.box, this.props.index);
    }

    render() {
        let boxStyle = {
            zIndex: 1,
            fontSize: this.props.box.fontSize+"pt",
            backgroundColor: "#"+this.props.box.background,
            borderColor: "#"+this.props.box.borderColor,
            borderWidth: this.props.box.borderThickness+"px",
            borderRadius: this.props.box.borderRadius+"px",
            borderStyle: "solid"
        }
        let rndStyle = {
            zIndex: 2
        }
        return (
            <div>
                <Rnd
                    position={{
                        x: this.props.box.x_position,
                        y: this.props.box.y_position}}
                    size={{
                        width: this.props.box.width,
                        height: this.props.box.height,
                    }}
                    style={rndStyle}
                    resizeHandleClasses={{
                        topRight: 'resize-handle',
                        topLeft: 'resize-handle',
                        bottomRight: 'resize-handle',
                        bottomLeft: 'resize-handle',
                    }}
                    bounds={'.diagram-full'}
                    onResizeStop={(e,d,r,c,p) => {
                        console.log(p);
                        this.props.box.x_position = p.x;
                        this.props.box.y_position = p.y;
                        this.props.box.width = parseInt(r.style.width.slice(0,-2));
                        this.props.box.height = parseInt(r.style.height.slice(0,-2));
                        console.log(this.props.box);
                        this.updateBoxList();
                        this.forceUpdate();
                    }}
                    onDragStop={(n,p) => {
                        this.props.box.x_position = p.x;
                        this.props.box.y_position = p.y;
                        console.log(this.props.box);
                        this.updateBoxList();
                        this.forceUpdate();
                    }}
                    >
                    <div className="diagram-box" style={boxStyle} onClick={() => this.showBorders()} >
                        <div className="diagram-label">
                            {this.props.box.name}
                        </div>
                    </div>
                </Rnd>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        todoLists: state.firestore.data,
        auth: state.firebase.auth
    };
};

export default compose(
    connect(mapStateToProps),
    firestoreConnect([
      { collection: 'wireframesLists' },
    ]),
)(LabelBox);