import React, { Component } from 'react';
import { Redirect } from 'react-router-dom'
import { connect } from 'react-redux';
import { compose } from 'redux';
import { firestoreConnect } from 'react-redux-firebase';
import { getFirestore } from 'redux-firestore';
import {Modal, Button, Textarea, Row, Col} from 'react-materialize';
import { Link } from 'react-router-dom';
import ContainerBox from './ContainerBox.js';
import LabelBox from './LabelBox.js';
import ButtonBox from './ButtonBox.js';
import TextFieldBox from './TextFieldBox.js';
import { ChromePicker } from 'react-color';

class EditScreen extends Component {
    constructor(props){
        super(props);
        const fireStore = getFirestore();
        fireStore.collection('wireframesLists').doc(this.props.todoList.id).update({
            time: new Date().getTime()
        });
        this.index = 0;
        this.diagramName = this.props.todoList.name;
        this.diagramHeight = this.props.todoList.diagramHeight;
        this.diagramWidth = this.props.todoList.diagramWidth;
        this.diagramHeightTemp = this.props.todoList.diagramHeight.toString();
        this.diagramWidthTemp = this.props.todoList.diagramWidth.toString();
        this.boxList = new Array();
        for (let i = 0; i < this.props.todoList.items.length; i++){
            this.boxList[i] = new Object();
            this.boxList[i].key = this.props.todoList.items[i].key;
            this.boxList[i].x_position = this.props.todoList.items[i].x_position;
            this.boxList[i].y_position = this.props.todoList.items[i].y_position;
            this.boxList[i].height = this.props.todoList.items[i].height;
            this.boxList[i].width = this.props.todoList.items[i].width;
            this.boxList[i].type = this.props.todoList.items[i].type;
            this.boxList[i].name = this.props.todoList.items[i].name;
            this.boxList[i].fontSize = this.props.todoList.items[i].fontSize;
            this.boxList[i].background = this.props.todoList.items[i].background;
            this.boxList[i].borderColor = this.props.todoList.items[i].borderColor;
            this.boxList[i].borderThickness = this.props.todoList.items[i].borderThickness;
            this.boxList[i].borderRadius = this.props.todoList.items[i].borderRadius;
        }
        console.log(this.boxList);
    }
    state = {
        name: '',
        redirect: false,
        zoomLevel: 1,
        currentIndex: null,
        currentName: null,
        currentFont: null,
        currentBg: "#111111",
        currentBgColor: "#111111",
        currentBgThick: null,
        currentBgRadius: null,
        dimensionsChanged: false,
        displayColorPicker: false,
        displayColorPickerBG: false,
        zoomMultiplier: 1,
    }

    changeZoom = (j) => {
        let temp = this.state.zoomLevel*j;
        let checkHeight = parseInt(window.getComputedStyle(document.querySelector('.diagram-full')).height.toString().slice(0,-2))*j;
        let checkWidth = parseInt(window.getComputedStyle(document.querySelector('.diagram-full')).width.toString().slice(0,-2))*j;

        if(checkHeight < 10 || checkHeight >= 5000){
            return;
        }
        if(checkWidth < 10 || checkWidth >= 5000){
            return;
        }


        let temp2 = this.state.zoomMultiplier/j;
        this.setState({zoomMultiplier: temp2});
        this.setState({zoomLevel: temp});
        let tempHeight = parseInt(window.getComputedStyle(document.querySelector('.diagram-full')).height.toString().slice(0,-2))*j.toString()+"px";
        let tempWidth = parseInt(window.getComputedStyle(document.querySelector('.diagram-full')).width.toString().slice(0,-2))*j.toString()+"px";

        document.querySelector('.diagram-full').style.height = tempHeight;
        document.querySelector('.diagram-full').style.width = tempWidth;

        let tempList = new Array();
        for (let i = 0; i < this.boxList.length; i++){
            tempList[i] = new Object();
            tempList[i].key = this.boxList[i].key;
            tempList[i].x_position = this.boxList[i].x_position*j;
            tempList[i].y_position = this.boxList[i].y_position*j;
            tempList[i].height = this.boxList[i].height*j;
            tempList[i].width = this.boxList[i].width*j;
            tempList[i].type = this.boxList[i].type;
            tempList[i].name = this.boxList[i].name;
            tempList[i].fontSize = this.boxList[i].fontSize*j;
            tempList[i].background = this.boxList[i].background;
            tempList[i].borderColor = this.boxList[i].borderColor;
            tempList[i].borderThickness = this.boxList[i].borderThickness*j;
            tempList[i].borderRadius = this.boxList[i].borderRadius*j;
        }
        this.boxList = tempList;
        console.log(tempList);
        console.log(this.boxList);
        this.forceUpdate();
    }

    saveChanges = () =>{
        const { id } = this.props.match.params;
        let j = this.state.zoomLevel;

        let tempList = new Array();
        for (let i = 0; i < this.boxList.length; i++){
            tempList[i] = new Object();
            tempList[i].key = this.boxList[i].key;
            tempList[i].x_position = this.boxList[i].x_position/j;
            tempList[i].y_position = this.boxList[i].y_position/j;
            tempList[i].height = this.boxList[i].height/j;
            tempList[i].width = this.boxList[i].width/j;
            tempList[i].type = this.boxList[i].type;
            tempList[i].name = this.boxList[i].name;
            tempList[i].fontSize = this.boxList[i].fontSize/j;
            tempList[i].background = this.boxList[i].background;
            tempList[i].borderColor = this.boxList[i].borderColor;
            tempList[i].borderThickness = this.boxList[i].borderThickness/j;
            tempList[i].borderRadius = this.boxList[i].borderRadius/j;
        }

        const fireStore = getFirestore();
        fireStore.collection('wireframesLists').doc(id).update({
            name: this.diagramName,
            diagramHeight: this.diagramHeight,
            diagramWidth: this.diagramWidth,
            items: tempList,
        });
    }

    close = () =>{
        this.setState({redirect: true});
    }

    setItemProperty(item){
        this.diagramName = document.getElementById('diagram-name').value;
        this.diagramWidthTemp = document.getElementById('diagram-width').value;
        this.diagramHeightTemp = document.getElementById('diagram-height').value;

        if (this.diagramWidthTemp != this.diagramWidth){
            this.setState({dimensionsChanged: true});
        }
        else if (this.diagramHeightTemp != this.diagramHeight){
            this.setState({dimensionsChanged: true});
        }
        else{
            this.setState({dimensionsChanged: false});
        }
        this.forceUpdate();
    }

    updateDimensions = () => {
        if (parseInt(document.getElementById('diagram-width').value) < 1 || isNaN(parseInt(document.getElementById('diagram-width').value))){
            document.getElementById('diagram-width').value = '1';
            this.diagramWidthTemp = 1;
        }
        if (parseInt(document.getElementById('diagram-height').value) < 1 || isNaN(parseInt(document.getElementById('diagram-height').value))){
            document.getElementById('diagram-height').value = '1';
            this.diagramHeightTemp = 1;
        }
        if (parseInt(document.getElementById('diagram-width').value) > 5000){
            document.getElementById('diagram-width').value = '5000';
            this.diagramWidthTemp = 5000;
        }
        if (parseInt(document.getElementById('diagram-height').value) > 5000){
            document.getElementById('diagram-height').value = '5000';
            this.diagramHeightTemp = 5000;
        }
        let j = parseInt(this.diagramWidthTemp)/(this.diagramWidth);
        let k = parseInt(this.diagramHeightTemp)/(this.diagramHeight);

        this.diagramWidth = parseInt(this.diagramWidthTemp);
        this.diagramHeight = parseInt(this.diagramHeightTemp);

        let tempHeight = this.diagramHeightTemp+"px";
        let tempWidth = this.diagramWidthTemp+"px";

        document.querySelector('.diagram-full').style.height = tempHeight;
        document.querySelector('.diagram-full').style.width = tempWidth;

        let tempList = new Array();
        for (let i = 0; i < this.boxList.length; i++){
            tempList[i] = new Object();
            tempList[i].key = this.boxList[i].key;
            tempList[i].x_position = this.boxList[i].x_position*j*this.state.zoomMultiplier;
            tempList[i].y_position = this.boxList[i].y_position*k*this.state.zoomMultiplier;
            tempList[i].height = this.boxList[i].height*k*this.state.zoomMultiplier;
            tempList[i].width = this.boxList[i].width*j*this.state.zoomMultiplier;
            tempList[i].type = this.boxList[i].type;
            tempList[i].name = this.boxList[i].name;
            tempList[i].fontSize = this.boxList[i].fontSize*this.state.zoomMultiplier;
            tempList[i].background = this.boxList[i].background;
            tempList[i].borderColor = this.boxList[i].borderColor;
            tempList[i].borderThickness = this.boxList[i].borderThickness*this.state.zoomMultiplier;
            tempList[i].borderRadius = this.boxList[i].borderRadius*this.state.zoomMultiplier;
        }

        this.setState({dimensionsChanged: false});
        this.setState({zoomLevel: 1});
        this.setState({zoomMultiplier: 1});
        this.boxList = tempList;
        console.log(tempList);
        console.log(this.boxList);
        this.forceUpdate();
    }

    setProperties(){
        this.boxList[this.state.currentIndex].name = document.getElementById('property-name').value;
        this.boxList[this.state.currentIndex].fontSize = document.getElementById('property-font').value*this.state.zoomLevel;
        this.boxList[this.state.currentIndex].background = document.getElementById('property-bg').value.slice(1,);
        this.boxList[this.state.currentIndex].borderColor = document.getElementById('property-bg-color').value.slice(1,);
        this.boxList[this.state.currentIndex].borderThickness = document.getElementById('property-bg-thick').value*this.state.zoomLevel;
        this.boxList[this.state.currentIndex].borderRadius = document.getElementById('property-bg-radius').value*this.state.zoomLevel;
        this.forceUpdate();
    }

    deleteList = () => {
        const fireStore = getFirestore();
        fireStore.collection('wireframesLists').doc(this.props.todoList.id).delete().then(function() {
            console.log("List removed.");
        }).catch(function(error) {
            console.error("Error removing list: ", error);
        });
    }

    updateList(box, index){
        if(box === null){
            this.setState({currentIndex: index});
        }
        else{
            this.boxList[index] = box;
            console.log(this.boxList);
        }
        this.forceUpdate();
    }

    noCurrentItem(){
        this.setState({currentIndex: null});
    }

    addBox(s){
        let temp = new Object();
        temp.key = this.boxList.length;
        temp.x_position = this.diagramWidth*this.state.zoomLevel/2 - (50*this.state.zoomLevel);
        temp.y_position = this.diagramHeight*this.state.zoomLevel/2 - (25*this.state.zoomLevel);
        temp.height = 50*this.state.zoomLevel;;
        temp.width = 100*this.state.zoomLevel;;
        temp.type = s;
        temp.name = "";
        temp.fontSize = 12*this.state.zoomLevel;
        temp.background = "FFFFFF";
        temp.borderColor = "000000";
        temp.borderThickness = 0;
        temp.borderRadius = 0;

        if(s === "button"){
            temp.background = "E6E6E6";
            temp.borderThickness = 1*this.state.zoomLevel;;
        }

        if(s === "container"){
            temp.borderThickness = 1*this.state.zoomLevel;;
        }

        if(s === "label"){
            temp.name = "Sample";
        }

        if(s === "textfield"){
            temp.borderThickness = 1*this.state.zoomLevel;;
        }


        this.boxList.push(temp);
        console.log(this.boxList);
        this.forceUpdate();
    }

    removeBox(){
        if (this.state.currentIndex === null){
            return;
        }
        let tempList = new Array();
        let j = 0;
        for (let i = 0; i < this.boxList.length; i++){
            if (i === this.state.currentIndex){
                // Do nothing
            }
            else{
                tempList[j] = new Object();
                tempList[j].key = j;
                tempList[j].x_position = this.boxList[i].x_position;
                tempList[j].y_position = this.boxList[i].y_position;
                tempList[j].height = this.boxList[i].height;
                tempList[j].width = this.boxList[i].width;
                tempList[j].type = this.boxList[i].type;
                tempList[j].name = this.boxList[i].name;
                tempList[j].fontSize = this.boxList[i].fontSize;
                tempList[j].background = this.boxList[i].background;
                tempList[j].borderColor = this.boxList[i].borderColor;
                tempList[j].borderThickness = this.boxList[i].borderThickness;
                tempList[j].borderRadius = this.boxList[i].borderRadius;
                j++;
            }
        }
        this.boxList = tempList;
        this.forceUpdate();
    }

    duplicateBox(){
        if (this.state.currentIndex === null){
            return;
        }
        let temp = new Object();
        temp.key = this.boxList.length;
        temp.x_position = this.boxList[this.state.currentIndex].x_position+(100*this.state.zoomLevel);
        temp.y_position = this.boxList[this.state.currentIndex].y_position+(100*this.state.zoomLevel);
        temp.height = this.boxList[this.state.currentIndex].height;
        temp.width = this.boxList[this.state.currentIndex].width;

        if (temp.x_position+temp.width > this.diagramWidth*this.state.zoomLevel){
            temp.x_position = this.diagramWidth*this.state.zoomLevel - temp.width;
        }
        if (temp.y_position+temp.height > this.diagramHeight*this.state.zoomLevel){
            temp.y_position = this.diagramHeight*this.state.zoomLevel - temp.height;
        }

        temp.type = this.boxList[this.state.currentIndex].type;
        temp.name = this.boxList[this.state.currentIndex].name;
        temp.fontSize = this.boxList[this.state.currentIndex].fontSize;
        temp.background = this.boxList[this.state.currentIndex].background;
        temp.borderColor = this.boxList[this.state.currentIndex].borderColor;
        temp.borderThickness = this.boxList[this.state.currentIndex].borderThickness;
        temp.borderRadius = this.boxList[this.state.currentIndex].borderRadius;

        this.boxList.push(temp);
        console.log(this.boxList);
        this.forceUpdate();
    }

    componentDidMount(){
        document.addEventListener("keydown", this.handleKeyEvent);
        document.addEventListener("keyup", this.toggleCtrl);
    }
    componentWillUnmount() {
        document.removeEventListener("keydown", this.handleKeyEvent);
        document.removeEventListener("keyup", this.toggleCtrl);
    }
    handleKeyEvent = (event) => {
        if (event.keyCode === 68 && this.ctrlToggled){
            event.preventDefault();
            this.duplicateBox();
            this.forceUpdate();
        }
        else if (event.keyCode === 46){
            event.preventDefault();
            this.removeBox();
            this.forceUpdate();
        }
        else if (event.keyCode === 17) {
            this.ctrlToggled = true;
        }
    }
    toggleCtrl = (event) => {
        if (event.keyCode === 17){
            this.ctrlToggled = false;
        }
    }

    openPicker = () =>{
        this.setState({ displayColorPicker: !this.state.displayColorPicker });
        this.setState({currentBgColor: document.getElementById('property-bg-color').value});
    }

    closePicker = () =>{
        this.setState({ displayColorPicker: false });
    }
    
    handleChangeComplete = (color) =>{
        document.getElementById('property-bg-color').value = color.hex;
        this.setState({currentBgColor: color.hex});
        this.setProperties();
    }

    openPickerBG = () =>{
        this.setState({ displayColorPickerBG: !this.state.displayColorPickerBG });
        this.setState({currentBg: document.getElementById('property-bg').value});
    }

    closePickerBG = () =>{
        this.setState({ displayColorPickerBG: false });
    }

    handleChangeCompleteBG = (color) =>{
        document.getElementById('property-bg').value = color.hex;
        this.setState({currentBg: color.hex});
        this.setProperties();
    }

    render() {
        const auth = this.props.auth;

        if (!auth.uid) {
            return <Redirect to="/" />;
        }

        if (this.state.redirect){
            return <Redirect to="/" />;
        }

        let diagramStyle = {
            height: this.diagramHeight.toString()+'px',
            width: this.diagramWidth.toString()+'px',
        }
        let popover = {
            position: 'absolute',
            zIndex: '2',
        }
        let cover = {
            position: 'fixed',
            top: '0px',
            right: '0px',
            bottom: '0px',
            left: '0px',
        }

        return (
            <div className="diagram-container">
                <div className="panel-left">
                    <div className="zoom-header">
                        <img src={"../zoomin.png"} className="zoomPic" onClick={() => this.changeZoom(2)} />
                        <img src={"../zoomout.png"} className="zoomPic" onClick={() => this.changeZoom(.5)} />
                        <button className="zoomOption" onClick={() => this.saveChanges()}>Save</button>
                        <button className="zoomOption" onClick={() => this.close()}>Close</button>
                    </div>
                    <br/>
                    <a className="add-box" onClick={()=> this.addBox("container")}>
                        <div className="container-sample">

                        </div>
                        <div className="container-option">
                            Container
                        </div>
                    </a>
                    <hr />
                    <a className="add-box" onClick={()=> this.addBox("label")}>
                        <div className="container-option">
                            <p className="input-example">Prompt for input:</p>
                            Label
                        </div>
                    </a>
                    <hr />
                    <a className="add-box" onClick={()=> this.addBox("button")}>
                        <div>
                            <button className="button-sample">
                                Submit
                            </button>
                        </div>
                        <div className="container-option">
                            Button
                        </div>
                    </a>
                    <hr />
                    <a className="add-box" onClick={()=> this.addBox("textfield")}>
                        <div>
                            <input className="input-example browser-default normal" type="text" readOnly>
                                
                            </input>
                        </div>
                        <div className="container-option">
                            Textfield
                        </div>
                    </a>
                </div>
                <div className="diagram-full-container">
                    <div className="diagram-full" style={diagramStyle}>
                    {this.boxList.map((singleBox)=>{
                        if (singleBox.type === "container"){
                            return <ContainerBox
                                zoomLevel = {this.state.zoomLevel}
                                box = {singleBox}
                                index = {singleBox.key}
                                updateList = {this.updateList.bind(this)}
                                noCurrentItem = {this.noCurrentItem.bind(this)}/>
                        }
                        else if (singleBox.type === "label"){
                            return <LabelBox
                                zoomLevel = {this.state.zoomLevel}
                                box = {singleBox}
                                index = {singleBox.key}
                                updateList = {this.updateList.bind(this)}
                                noCurrentItem = {this.noCurrentItem.bind(this)}/>
                        }
                        else if (singleBox.type === "button"){
                            return <ButtonBox
                                zoomLevel = {this.state.zoomLevel}
                                box = {singleBox}
                                index = {singleBox.key}
                                updateList = {this.updateList.bind(this)}
                                noCurrentItem = {this.noCurrentItem.bind(this)}/>
                        }
                        else if (singleBox.type === "textfield"){
                            return <TextFieldBox
                                zoomLevel = {this.state.zoomLevel}
                                box = {singleBox}
                                index = {singleBox.key}
                                updateList = {this.updateList.bind(this)}
                                noCurrentItem = {this.noCurrentItem.bind(this)}/>
                        }
                    })}
                    </div>
                </div>
                <div className="panel-right properties stay">
                    <div className="diagram-options stay">
                        <label className="option stay">Properties:</label>
                        <input className="stay" type="text" id="property-name" onChange={() => this.setProperties()}>{this.state.currentName}</input>
                        <br/>
                        <label className="option stay">Font Size:</label>
                        <input className="stay" type="text" id="property-font" onChange={() => this.setProperties()}>{this.state.currentFont}</input>
                        <br/>

                        <label className="option stay">Background:</label>
                        <input className="stay" type="text" id="property-bg" onClick={()=>this.openPickerBG()} onChange={() => this.setProperties()}></input>
                        { this.state.displayColorPickerBG ? <div className="stay" style={ popover }>
                        <div className="stay" style={ cover } onClick={() => this.closePickerBG()}/>
                        <ChromePicker
                            color={this.state.currentBg}
                            onChangeComplete={ this.handleChangeCompleteBG }/>
                        </div> : null }
                        <br/>

                        <label className="option stay">Border Color:</label>
                        <input className="stay" type="text" id="property-bg-color" onClick={()=>this.openPicker()} onChange={() => this.setProperties()}></input>
                        { this.state.displayColorPicker ? <div className="stay" style={ popover }>
                        <div className="stay" style={ cover } onClick={() => this.closePicker()}/>
                        <ChromePicker
                            color={this.state.currentBgColor}
                            onChangeComplete={ this.handleChangeComplete }/>
                        </div> : null }
                        <br/>

                        <label className="option stay">Border Thickness:</label>
                        <input className="stay" type="text" id="property-bg-thick" onChange={() => this.setProperties()}>{this.state.currentBgThick}</input>
                        <br/>
                        <label className="option stay">Border Radius:</label>
                        <input className="stay" type="text" id="property-bg-radius" onChange={() => this.setProperties()}>{this.state.currentBgRadius}</input>
                    </div>
                </div>
                <div className="panel-right main-properties">
                    <div className="diagram-options">
                        <label className="option">Name:</label>
                        <input type="text" id="diagram-name" onChange={() => this.setItemProperty()} value={this.diagramName}></input>
                        <br/>
                        <label className="option">Diagram Height:</label>
                        <input type="text" id="diagram-height" onChange={() => this.setItemProperty()} value={this.diagramHeightTemp}></input>
                        <br/>
                        <label className="option">Diagram Width:</label>
                        <input type="text" id="diagram-width" onChange={() => this.setItemProperty()} value={this.diagramWidthTemp}></input>
                        <br/>
                        <button onClick={() => this.updateDimensions()} disabled={!this.state.dimensionsChanged}>Update</button>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state.firestore.data);
  const { id } = ownProps.match.params;
  const { wireframesLists } = state.firestore.data;
  const todoList = wireframesLists ? wireframesLists[id] : null;
  todoList.id = id;

  return {
    todoList,
    wireframesLists,
    auth: state.firebase.auth,
  };
};

export default compose(
  connect(mapStateToProps),
  firestoreConnect([
    { collection: 'wireframesLists' },
  ]),
)(EditScreen);