import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { compose } from 'redux';
import TodoListCard from './TodoListCard';
import { Modal, Button, Row, Col } from 'react-materialize';
import { getFirestore } from 'redux-firestore';

class TodoListLinks extends React.Component {
    deleteList = (list) =>{
        console.log(list);
        const fireStore = getFirestore();
        fireStore.collection('wireframesLists').doc(list.id).delete().then(function() {
            console.log("List removed.");
        }).catch(function(error) {
            console.error("Error removing list: ", error);
        });
        this.forceUpdate();
    }
    render() {
        const todoLists = this.props.todoLists;
        console.log(todoLists);
        return (
            <div className="todo-lists section">
                {todoLists && todoLists.map(todoList => (
                    <Row>
                        <Col s={9}>
                            <Link to={'/wireframesList/' + todoList.id} key={todoList.id}>
                                <TodoListCard todoList={todoList}/>
                            </Link>
                        </Col>
                        <Col s={3}>
                            <Modal header="Delete List" className="delete-modal" id="deleteModal" trigger={
                                <div className="card red z-depth-0 pointer-div">
                                    <div className="card-content black-text text-darken-3">
                                        <span className="card-title deleteListButton">X</span>
                                    </div>
                                </div>
                            }>
                                <div id="todo_delete_box" className="inactive">
                                    <b>Are you sure you want to delete this list?</b>
                                    <br/><br/>
                                    <Button className="btn-padding" modal="close" id="delete_confirm"
                                    onClick={() => this.deleteList(todoList)}>Yes</Button>
                                    <Button className="btn-padding" modal="close" id="delete_cancel">No</Button>
                                    <br/><br/>
                                    The list will not be retreivable.
                                </div>
                            </Modal>
                        </Col>
                    </Row>
                ))}
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        todoLists: state.firestore.ordered.wireframesLists,
        auth: state.firebase.auth,
    };
};

export default compose(connect(mapStateToProps))(TodoListLinks);