import React from 'react'
import { connect } from 'react-redux';
import todoJson from './TestTodoListData.json'
import { getFirestore } from 'redux-firestore';

class DatabaseTester extends React.Component {

    // NOTE, BY KEEPING THE DATABASE PUBLIC YOU CAN
    // DO THIS ANY TIME YOU LIKE WITHOUT HAVING
    // TO LOG IN
    handleClear = () => {
        const fireStore = getFirestore();
        fireStore.collection('wireframesLists').get().then(function(querySnapshot){
            querySnapshot.forEach(function(doc) {
                console.log("deleting " + doc.id);
                fireStore.collection('wireframesLists').doc(doc.id).delete();
            })
        });
    }

    handleReset = () => {
        console.log(todoJson);
        const fireStore = getFirestore();
        todoJson.wireframeLists.forEach(todoListJson => {
            let temp = new Date().getTime();
            console.log(temp);
            fireStore.collection('wireframesLists').add({
                    key: todoListJson.key,
                    name: todoListJson.name,
                    diagramHeight: todoListJson.diagramHeight,
                    diagramWidth: todoListJson.diagramWidth,
                    email: todoListJson.email,
                    items: todoListJson.items,
                    time: temp
                }).then(() => {
                    console.log("DATABASE RESET");
                }).catch((err) => {
                    console.log(err);
                });
        });
    }

    render() {
        return (
            <div className="database-tester">
                <button onClick={this.handleClear}>Clear Database</button>
                <button onClick={this.handleReset}>Reset Database</button>
            </div>)
    }
}

const mapStateToProps = function (state) {
    return {
        auth: state.firebase.auth,
        firebase: state.firebase
    };
}

export default connect(mapStateToProps)(DatabaseTester);