import React, { Component } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { NavLink, Redirect } from 'react-router-dom';
import { firestoreConnect } from 'react-redux-firebase';
import TodoListLinks from './TodoListLinks'
import { getFirestore } from 'redux-firestore';
import { Button } from 'react-materialize';

class HomeScreen extends Component {
    state = {
        redirect: false,
        redirect_list: null,
    }
    handleNewList = () => {
        const fireStore = getFirestore();
        let temp = new Date().getTime();
        let l = 0;
        if(this.props.wireframesLists === null){
            // Do nothing
        }
        else{
            l = Object.keys(this.props.wireframesLists).length;
        }
        fireStore.collection('wireframesLists').add({
                key: l,
                name: "Unknown",
                email: this.props.auth.email,
                diagramHeight: 1000,
                diagramWidth: 1000,
                items: new Array(),
                time: temp
            }).then((list) => {
                console.log("List Created");
                console.log(list.id);
                this.setState({redirect_list: list.id});
                this.setState({redirect: true});
            }).catch((err) => {
                console.log(err);
            });
        this.forceUpdate();
    }

    render() {
        if (!this.props.auth.uid) {
            return <Redirect to="/login" />;
        }

        if(this.state.redirect){
            return <Redirect to={"/wireframesList/"+ this.state.redirect_list} key={this.state.redirect_list} />;
        }

        return (
            <div className="dashboard container">
                <div className="row">
                    <div className="col s12 m4">
                        <TodoListLinks />
                    </div>

                    <div className="col s8">
                        <div className="banner">
                            WireFramer™️
                        </div>
                        
                        <div className="home_new_list_container">
                                <Button large className="home_new_list_button btn-padding" onClick={this.handleNewList}>
                                    Create a New Diagram
                                </Button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    const { wireframesLists } = state.firestore.data;
    return {
        wireframesLists,
        auth: state.firebase.auth
    };
};

export default compose(
    connect(mapStateToProps),
    firestoreConnect(props => {
        if (!props.auth.uid) 
        {
            return [];
        }
        else
        {
            return [
            { collection: 'wireframesLists',
                where: ['email', '==', props.auth.email],
                orderBy: ['time', 'desc'] }];
        }
    }),
)(HomeScreen);